.MercMainContainer_CarouselContainer {
  overflow: hidden;
  position: relative;
  cursor: grab;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  background-color: black;
}

.MercMainContainer_CarouselContainer--NoPointerEvents {
  pointer-events: none;
}

.MercMainContainer_CarouselContainer_LeftButton {
  position: absolute;
  background: transparent;
  top: 50%;
  left: 10%;
  z-index: 2;
  width: 34px;
  height: 18px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: none;
}

.MercMainContainer_CarouselContainer_RightButton {
  position: absolute;
  background: transparent;
  top: 50%;
  right: 10%;
  z-index: 2;
  width: 34px;
  height: 18px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: none;
}

@media all and (max-width: 1023px) {
  .MercMainContainer_CarouselContainer_LeftButton,
  .MercMainContainer_CarouselContainer_RightButton {
    display: none;
  }
}
.MercMainContainer_CarouselContainer_SlideTrackerContainer {
  position: absolute;
  width: 100px;
  height: 12px;
  left: 50%;
  bottom: 15%;
  margin-left: -42px;
  display: flex;
  justify-content: space-evenly;
  z-index: 4;
}

.MercMainContainer_CarouselContainer_SlideTrackerContainer_SingleCircle {
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-color: #5f5f5f;
  border: 2px solid transparent;
}

.MercMainContainer_CarouselContainer_SlideTrackerContainer_SingleCircle--Active {
  border-color: white !important;
  background-color: white;
}

@media all and (max-width: 1123px) {
  .MercMainContainer_CarouselContainer_SlideTrackerContainer {
    bottom: 10%;
  }
}
@media all and (max-width: 700px) {
