    bottom: 2.5%;
  }
}
@media all and (max-width: 318px) {
  .MercMainContainer_CarouselContainer_SlideTrackerContainer {
    bottom: 5px;
  }
}
.MercMainContainer_CarouselContainer_Slider {
  display: grid;
  grid-template-columns: repeat(7, 100vw);
  transition: transform 1.5s ease-out;
}

.MercMainContainer_CarouselContainer_Slider--NoTransition {
  transition: none;
}

.MercMainContainer_CarouselContainer_Slider_SingleItemsContainer {
  position: relative;
  width: 100vw;
  height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
}

.MercMainContainer_CarouselContainer_Slider_SingleItemsContainer_BackgroundDiv {
  background-position: center;
  background-size: cover;
  width: 100vw;
  height: 95vh;
  opacity: 0.3;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

@media all and (max-width: 1023px) {
