.addFadeInUpAnimation, .MercMainContainer_NeonWavesContainer_TextBox_ButtonBox button, .MercMainContainer_NeonWavesContainer_TextBox_Title, .MercMainContainer_NeonWavesContainer_TextBox_Logo img {
  animation-name: fadeInUp;
  animation-duration: 0.5s;
  animation-delay: 0.5s;
  animation-fill-mode: backwards;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-clip-path: inset(0px 0px 30px);
            clip-path: inset(0px 0px 30px);
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    -webkit-clip-path: inset(0px);
            clip-path: inset(0px);
    transform: translate(0, 0);
  }
}
.animationDelayNone {
  animation-delay: 0s;
}

.animationDelay0125 {
  animation-delay: 0.125s;
}

.animationDelay_025 {
  animation-delay: 0.25s;
}

.animationDelay_0375 {
  animation-delay: 0.375s;
}

.animationDelay_0425 {
  animation-delay: 0.425s;
}

.animationDelay_050 {
  animation-delay: 0.5s;
}

.animationDelay_075 {
  animation-delay: 0.75s;
}

.animationDelay_090 {
  animation-delay: 0.9s;
}

.animationDelay_1 {
  animation-delay: 1s;
}

.animationTimingFunctionEaseOut {
  animation-timing-function: ease-out;
}

