.MercMainContainer_CookiesContainer {
  position: fixed;
  width: 100%;
  height: 25vh;
  bottom: 0;
  left: 0;
  z-index: 10;
  transition: transform 1s ease;
}

.MercMainContainer_CookiesContainer--NotVisible {
  transform: translateY(100%);
}

.MercMainContainer_CookiesContainer--NoCookieAnimation {
  transition: none;
}

.MercMainContainer_CookiesContainer_BGBox {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.85;
  background-color: black;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.MercMainContainer_CookiesContainer_InnerContainer {
  width: 1070px;
  margin-inline: auto;
  color: white;
  margin-top: 35px;
  display: flex;
  justify-content: space-between;
}

.MercMainContainer_CookiesContainer_InnerContainer_TextBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 15px;
  font-size: 18px;
  line-height: 29px;
  font-weight: 400;
  width: 85%;
}

.MercMainContainer_CookiesContainer_InnerContainer_TextBox_Links {
  display: inline-block;
  cursor: pointer;
}

.MercMainContainer_CookiesContainer_InnerContainer_TextBox_Links:hover > .MercMainContainer_CookiesContainer_InnerContainer_TextBox_Links_BottomBorder {
  width: 100%;
}

.MercMainContainer_CookiesContainer_InnerContainer_TextBox_Links_BottomBorder {
  border-bottom: 2px solid #00a6e5;
  width: 33%;
  transition: 0.5s ease-out;
}

.MercMainContainer_CookiesContainer_InnerContainer_BtnBox {
  width: 15%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.MercMainContainer_CookiesContainer_BGBox_Btn {
  background-color: transparent;
  text-transform: uppercase;
  padding: 10px 25px;
  color: white;
  border: 3px solid white;
  font-size: 21px;
  line-height: 24px;
  font-weight: 400;
  cursor: pointer;
}

.MercMainContainer_CookiesContainer_BGBox_Btn:hover {
  background-color: white;
  color: black;
}

@media all and (max-width: 1023px) {
  .MercMainContainer_CookiesContainer {
    height: -moz-fit-content;
    height: fit-content;
  }
  .MercMainContainer_CookiesContainer_InnerContainer {
    flex-direction: column;
    max-width: 90%;
  }
  .MercMainContainer_CookiesContainer_InnerContainer_TextBox {
    width: 100%;
  }
  .MercMainContainer_CookiesContainer_InnerContainer_BtnBox {
    width: 100%;
    margin-top: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .MercMainContainer_CookiesContainer_BGBox_Btn {
    width: 100%;
  }
}/*# sourceMappingURL=Cookies.css.map */